<template>
    <p-data-table
        :value="semenList"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        dataKey="semenId"
        v-model:selection="selectedSemen"
        @row-select="onRowSelect"
    >
        <p-column-group type="header">
            <p-row>
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="Volume" colspan="1" />
                <p-column header="Concentration" colspan="1" />
                <p-column header="Total Count" colspan="1" />
                <p-column header="Motility" colspan="1" />
                <p-column header="Total Motility" colspan="1" />
                <p-column header="Progression" colspan="1" />
                <p-column header="Round Cells" colspan="1" />
                <p-column header="" />
            </p-row>
            <p-row>
                <p-column header="" />
                <p-column header="Semen ID" />
                <p-column header="Date" />
                <p-column header="Partner" />
                <p-column header="Viscosit" />
                <p-column header="PH" />

                <p-column header="Analys" />
                <p-column header="Analys" />
                <p-column header="Analys" />
                <p-column header="Analys" />
                <p-column header="Analys" />
                <p-column header="Analys" />
                <p-column header="Analys" />

                <p-column header="Comment" />
            </p-row>
        </p-column-group>

        <p-column selectionMode="single" headerStyle="width: 3rem" />

        <p-column field="semBarcode" />
        <p-column field="semenDate" />
        <p-column field="partnerId" />
        <p-column field="viscosity" />
        <p-column field="ph" />

        <p-column field="volume" />

        <p-column field="concentration" />

        <p-column field="totalCount" />

        <p-column field="motility" />

        <p-column field="totalMotility" />

        <p-column field="progression" />

        <p-column field="roundCells" />

        <p-column field="comment" style="min-width: 280px;" />

        <template #footer>
            <p-button
                label="Create"
                class="p-mr-2 p-button-success"
                @click="create"
                v-if="hasCreatePermission"
            />
            <p-button
                label="Edit"
                class="p-mr-2"
                @click="edit"
                :disabled="selectedSemen == null"
                v-if="hasUpdatePermission"
            />
            <p-button
                label="Delete"
                class="p-mr-2 p-button-danger"
                @click="deleteSemen"
                :disabled="selectedSemen == null"
                v-if="hasDeletePermission"
            />
            <p-button
                label="PDF Report"
                class="p-mr-2 p-button-secondary"
                @click="openPdfReport"
            />
            <p-button
                label="PDF Reports (Standard)"
                class="p-mr-2 p-button-secondary"
                @click="openPdfReportStandard"
            />
        </template>
    </p-data-table>

    <semen-create-edit-dailog :type="dialog.type" :display="dialog.display" />
</template>

<script>
import { onMounted, provide } from "vue";
import { useDialog, useSemenSetup } from "@/composable/semen";

import SemenCreateEditDailog from "@/components/semen/create-edit-dialog.vue";
import { usePermissionCheck } from "@/composable/users";

export default {
    components: {
        SemenCreateEditDailog
    },
    setup() {
        const {
            store,
            toast,
            semenList,
            selectedSemen,
            onRowSelect,
            getSemenList,
            deleteSemen,
            openPdfReport,
            openPdfReportStandard
        } = useSemenSetup();

        const { dialog, create, edit, close } = useDialog();
        provide("dialog", dialog);

        const {
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission
        } = usePermissionCheck();

        onMounted(() => {
            if (store.state.artCycle.patient.sex === "M") {
                getSemenList(store.state.artCycle.patient.id);
                return;
            }

            if (
                store.state.artCycle.patient.sex === "F" &&
                store.state.artCycle.partner.id != null
            ) {
                getSemenList(store.state.artCycle.partner.id);
            }
        });

        return {
            semenList,
            selectedSemen,
            onRowSelect,
            dialog,
            create,
            edit,
            close,
            deleteSemen,
            openPdfReport,
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission,
            openPdfReportStandard
        };
    }
};
</script>

<style scoped>
.p-datatable :deep() .p-datatable-tbody > tr.p-highlight {
    background: #052c65;
    color: #ffffff;
}
</style>
